import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { MexElement } from "./MexElement";
import { RectangularLineType } from "./RectangularLineType";

export class RectangularLine extends MexElement {
    constructor(model: BpmnModel) {
        super(model);

        this.LineType = RectangularLineType.H2V;
    }

    LineType: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.LineType = RectangularLineType.H2V;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "LineType", this.LineType);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.LineType = obj.LineType;
    }
}